import { getProductCount } from '../../helpers/product_counter'

import './style.css'

export default function MainSlider(props) {
    return (
        <div className='main-slider'>
            <img className='image' src='https://www.cnet.com/a/img/hu4itKtgK3j-6tbapbY4QekB0gg=/940x0/2020/04/10/61b41531-c828-4d90-90d8-9d4d2866f8ae/apple-watch-anniversary-2.jpg' />
            <div className='slider-content'>
                <div className='title'>Скидка <b>94%</b> в нашем магазине</div>
                <div className='product-name'>Apple Watch Series 6</div>
                <div className='product-count'>Осталось <b>{getProductCount()}</b> штук</div>
            </div>
        </div>
    )
}