export const getProductCount = () => {
    var startDate = new Date('2021-07-04T00:17:28.593Z');
    var now = new Date();
    // Do your operations
    var endDate = new Date('2021-07-06T10:17:28.593Z');
    var allSeconds = (endDate.getTime() - startDate.getTime()) / 1000;
    var elapsedSeconds = (endDate.getTime() - now.getTime()) / 1000;
    var totalProductCount = 48;
    var countNow = Math.floor(totalProductCount * elapsedSeconds / allSeconds) + 1;
    return countNow;
}