import Grid from '@material-ui/core/Grid';

import './style.css'

export default function ProductInfoSection(props) {
    return (
        <dic className='product-info-section'>
            <Grid container>
                <Grid item xs={12} md={6}>
                    <div className='product-image-container'>
                        <img className='product-image' src='https://phone-mechanic.com/wp-content/uploads/2021/03/42-alu-space-sport-black-nc-1up.png' />
                    </div>
                </Grid>
                <Grid item xs={12} md={6}>
                    <div className='product-details'>
                        <div className='name'>Apple Watch i6 (2021)</div>
                        <p>
                            <div className='sell-price'>25$</div> <div className='original-price'>429$</div>
                        </p>
                        <div className='about'>
                            <b>В комплекте:</b> Оригинальное зарядное устройство, запасной аккумлиятор, защитное стекло.
                            <br/>
                            <b>Цвет:</b> Черний
                            <br/>
                            <b>Доставка:</b> Бесплатная
                            <br/>
                            <b>Гарантия:</b> 1 год (сертификат в коробке)
                            <br/>
                            <b>Качество:</b> Оригинал
                        </div>
                    </div>
                </Grid>
            </Grid>
        </dic>
    )
}